* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.App {
  background-color: #292b34;
  height: 100vh;
  font-size: 20px;
  font-weight: bold;
  min-width: 23em;

}

.appWrap {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  height: 100%;
  width: 70%;
}

